/** @format */
.stake-container .img {
  width: 42px;
  height: 42px;
  margin: 10px 0;
}
.stake-container .liquidity-source-stake {
  width: 226px;
  height: 28px;
  background: #d6e4ff;
  padding: 0 12px;
  font-weight: 600;
  color: #909db4;
  border: 1px solid transparent;
  border-radius: 15px;
  margin-bottom: 20px;
  border-image-slice: 1;
}
.stake-container .liquidity-source-stake:hover {
  border: 1px solid #005aff;
}
.stake-container .liquidity-source-stake a {
  width: auto;
  height: 26px;
  color: #909db4;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.stake-container .liquidity-source-stake a img {
  width: 18px;
  height: 18px;
  margin: 0 3px;
}
.stake-container .liquidity-source-stake a .icon {
  width: 16px;
  height: 16px;
  fill: #909DB4;
  margin-left: 2px;
}
.stake-container .liquidity-logo {
  display: flex;
  align-items: center;
}
.stake-container .liquidity-logo .CoinLogo-img {
  width: 44px;
  height: 28px;
  margin-right: 8px;
}
.stake-container .liquidity-logo .Auto-img {
  width: 42px;
  height: 42px;
  margin-right: 8px;
}
.stake-container .liquidity-logo .NSDX-img {
  width: 32px;
  height: 32px;
  margin-right: 8px;
}
.stake-container .liquidity-name {
  font-size: 14px;
  font-weight: 550;
  color: #333333;
}
.stake-container .amount {
  background: #F2F4FC;
  border-radius: 8px;
  padding: 20px 12px;
  margin-top: 12px;
}
.stake-container .amount .amount-header {
  font-weight: 600;
  color: #909db4;
  display: flex;
  justify-content: space-between;
}
.stake-container .amount .amount-header span {
  font-weight: 600;
  color: #333333;
  font-size: 16px;
}
.stake-container .amount .amount-input {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 8px;
}
.stake-container .amount .amount-input input {
  font-size: 18px;
  font-weight: 600;
  color: #333333;
  padding: 0;
  margin-bottom: -5px;
}
.stake-container .amount .amount-input .max-btn {
  padding: 4px 8px;
  max-height: 20px;
  background: #d6e4ff;
  border-radius: 4px;
  font-weight: 600;
  line-height: 1;
  font-size: 12px;
  color: #005AFF;
}
.stake-container .amount .amount-input .max-btn:hover {
  background-color: #005aff;
  color: #ffffff;
}
.stake-container .stake-btn {
  width: 100%;
  height: 48px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 20px;
  font-weight: 600;
  color: #FFFFFF;
}
.stake-container .stake-btn span {
  font-size: 16px;
}
.stake-container .stake-btn:hover {
  background-color: #0d8fff;
}
.stake-container .ant-btn[disabled] {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  font-weight: 600;
  box-shadow: none;
  border-radius: 8px;
  color: rgba(0, 0, 0, 0.1);
}
@media (max-width: 750px) {
  .img {
    margin-top: 0px;
  }
}
