/** @format */
.symbol-chart {
  width: 580px;
  padding: 20px;
  background: #fff;
  border-radius: 8px;
  height: 100%;
}
.symbol-chart .title {
  display: flex;
  justify-content: space-between;
}
.symbol-chart .title .symbol-info {
  display: flex;
}
.symbol-chart .title .symbol-info .logo img {
  width: 40px;
  height: 40px;
}
.symbol-chart .title .symbol-info .symbol-name {
  margin-left: 8px;
}
.symbol-chart .title .symbol-info .symbol-name .name {
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  color: #666;
}
.symbol-chart .title .symbol-info .symbol-name .name span {
  font-size: 18px;
  color: #333333;
}
.symbol-chart .title .symbol-info .symbol-name .name .swap-symbol-tab {
  width: 20px;
  height: 20px;
  margin-left: 10px;
  background: url(../../img/swap/icon-change.png);
  background-size: 100% 100%;
}
.symbol-chart .title .symbol-info .symbol-name .name .swap-symbol-tab:hover {
  background: url(../../img/swap/icon-change-hover.png);
  background-size: 100% 100%;
}
.symbol-chart .title .symbol-info .symbol-name .source {
  color: #909DB4;
  margin-top: 6px;
  font-size: 14px;
}
.symbol-chart .title .symbol-info .symbol-name .source img {
  width: 14px;
  height: 14px;
  margin-left: 3px;
}
.symbol-chart .title .symbol-total {
  display: flex;
  align-items: center;
}
.symbol-chart .title .symbol-total .volume,
.symbol-chart .title .symbol-total .liquidity {
  margin-left: 53px;
}
.symbol-chart .title .symbol-total .premium {
  position: relative;
}
.symbol-chart .title .symbol-total .premium .leabl {
  font-size: 12px;
  font-weight: 500;
  color: #909DB4;
  display: flex;
}
.symbol-chart .title .symbol-total .premium p {
  display: none;
  width: 190px;
  position: absolute;
  background-color: #f7f7f7;
  top: 20px;
  left: 0;
  z-index: 99;
  padding: 10px;
  border-radius: 8px;
}
.symbol-chart .title .symbol-total .premium img {
  width: 16px;
  height: 16px;
  margin-left: 5px;
}
.symbol-chart .title .symbol-total .premium:hover p {
  display: block;
}
.symbol-chart .title .symbol-total .liquidity {
  margin-top: 0;
  text-align: right;
}
.symbol-chart .title .symbol-total .title {
  font-size: 12px;
  font-weight: 500;
  color: #909DB4;
}
.symbol-chart .title .symbol-total .text {
  font-size: 12px;
  font-weight: 600;
  color: #333333;
  margin-top: 8px;
}
.symbol-chart .chart-info {
  margin-top: 24px;
  display: flex;
  justify-content: space-between;
}
.symbol-chart .chart-info .symbol-price .price {
  font-size: 26px;
  font-weight: 600;
  color: #333333;
  line-height: 1;
  position: relative;
}
.symbol-chart .chart-info .symbol-price .price img {
  width: 14px;
  height: 14px;
  margin-left: 5px;
}
.symbol-chart .chart-info .symbol-price .price img:hover + .tips-text {
  display: block;
}
.symbol-chart .chart-info .symbol-price .price .tips-text {
  padding: 10px 12px;
  background: rgba(0, 90, 255, 0.04);
  border-radius: 4px;
  line-height: 1;
  color: #909DB4;
  position: absolute;
  top: 30px;
  right: -50px;
  display: none;
  z-index: 99;
}
.symbol-chart .chart-info .symbol-price .price .tips-text p {
  margin-top: 10px;
}
.symbol-chart .chart-info .symbol-price .rise {
  display: inline-block;
  margin-top: 8px;
  padding: 5px 8px;
  background: #E6EFFF;
  font-weight: 600;
  border-radius: 5px;
  color: #005AFF;
  font-size: 12px;
}
.symbol-chart .chart-info .symbol-price .rise .icon {
  width: 8px;
  height: 8px;
  fill: #005AFF;
  margin-left: 4px;
}
.symbol-chart .chart-info .time-type {
  display: flex;
  height: 100%;
  font-size: 12px;
  font-weight: 600;
}
.symbol-chart .chart-info .time-type span {
  cursor: pointer;
  padding: 4px 7px;
  border-radius: 4px;
  color: #909DB4;
}
.symbol-chart .chart-info .time-type span:not(:last-child) {
  margin-right: 20px;
}
@media (max-width: 750px) {
  .symbol-chart {
    width: 100%;
    padding: 10px;
    border-radius: 8px;
  }
  .symbol-chart .title {
    display: block;
    margin-top: 0px;
  }
  .symbol-chart .title .symbol-total {
    display: flex;
    justify-content: space-between;
    margin-top: 21px;
  }
  .symbol-chart .chart-view,
  .symbol-chart .chart-info {
    display: none;
  }
}
