/** @format */
.order-confirm-conntent .price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.order-confirm-conntent .price span {
  font-size: 14px;
  font-weight: 600;
  color: #333330;
}
.order-confirm-conntent .price img {
  width: 36px;
  height: 36px;
  margin-right: 8px;
}
.order-confirm-conntent .price .mint-assets {
  font-size: 18px;
  font-weight: 500;
  color: #333333;
}
.order-confirm-conntent .price .mint-assets span {
  font-size: 14px;
  font-weight: 500;
  color: #909db4;
}
.order-confirm-conntent .detail {
  padding: 20px;
  background: #f7f7f7;
  border-radius: 8px;
  margin-top: 19px;
}
.order-confirm-conntent .detail .detail-item {
  display: flex;
  justify-content: space-between;
}
.order-confirm-conntent .detail .detail-item:not(:first-child) {
  margin-top: 16px;
}
.order-confirm-conntent .detail .detail-item .leabl {
  font-size: 14px;
  color: #909db4;
}
.order-confirm-conntent .detail .detail-item .text {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
}
.order-confirm-conntent .warning {
  margin-top: 9px;
  background: rgba(254, 96, 18, 0.1);
  border-radius: 8px;
  font-size: 12px;
  color: #fe5d0d;
  padding: 9px 12px;
  display: flex;
  text-align: left;
  align-items: center;
}
.order-confirm-conntent .warning img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.order-confirm-conntent .warning span {
  line-height: 1.2;
}
.order-confirm-conntent .tips-available {
  margin-top: 20px;
  padding: 9px 10px;
  width: 100%;
  text-align: left;
  background: rgba(254, 96, 18, 0.1);
  border-radius: 8px;
}
.order-confirm-conntent .tips-available .content {
  color: #fe5d0d;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.order-confirm-conntent .tips-available .content span {
  line-height: 18px;
}
.order-confirm-conntent .tips-available .content img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.order-confirm-conntent .tips-available .content svg {
  width: 12px;
  height: 12px;
  margin-left: 2px;
  fill: #909DB4;
}
.order-confirm-conntent .confirm-btn {
  width: 155px;
  height: 40px;
  background: #005aff;
  border-radius: 8px;
  font-weight: 600;
  color: #ffffff;
  margin-top: 20px;
  padding: 0;
}
.order-confirm-conntent .confirm-btn .mintSpan {
  display: inline-block;
  width: 155px;
  height: 40px;
  font-size: 14px;
  line-height: 40px;
  text-align: center;
}
