/** @format */
.container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  background: #F7F8FB;
  color: #909DB4;
}
.container .sidebar {
  height: 100vh;
  width: 200px;
  background: #FFFFFF;
  position: relative;
  box-shadow: 10px 0px 50px 0px rgba(186, 200, 224, 0.2);
}
.container .sidebar .language-select {
  width: 160px;
  height: 36px;
  background: #fff;
  border-radius: 8px;
  -moz-user-select: none;
  /**/
  -webkit-user-select: none;
  /*webkit*/
  -ms-user-select: none;
  /*IE10*/
  -khtml-user-select: none;
  /**/
  user-select: none;
  position: relative;
}
.container .sidebar .language-select i {
  display: inline-block;
  width: 200px;
  height: 1px;
  background: #ececec;
  margin-bottom: 10px;
}
.container .sidebar .language-select .select-name {
  padding: 6px 16px;
  font-size: 12px;
  margin-top: 1px;
  color: #909db4;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.container .sidebar .language-select .select-name svg {
  width: 18px;
  height: 18px;
  fill: #909db4;
}
.container .sidebar .language-select .select-name span {
  font-size: 14px;
  margin-left: 5px;
}
.container .sidebar .language-select ul {
  position: absolute;
  top: -70px;
  left: 0;
  width: 115px;
  padding: 10px 12px;
  background: #fff;
  box-shadow: 10px 0px 50px 0px rgba(186, 200, 224, 0.2);
  margin-top: 0px;
  z-index: 5;
}
.container .sidebar .language-select ul li {
  font-size: 14px;
  font-weight: 500;
  color: #909db4;
  text-align: center;
  line-height: 24px;
  cursor: pointer;
}
.container .sidebar .language-select ul li:hover {
  background: #f3f7fe;
  border-radius: 10px;
}
.container .sidebar .language-select ul li + li {
  margin-top: 15px;
}
.container .sidebar .h5-nav {
  display: none;
}
.container .sidebar .menu-list-block {
  height: 100%;
}
.container .sidebar .menu-list-block .logo {
  padding-left: 20px;
  padding-top: 20px;
  position: relative;
}
.container .sidebar .menu-list-block .logo img {
  height: 40px;
}
.container .sidebar .menu-list-block .logo .toggle-btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  position: absolute;
  right: 0px;
  top: 32px;
}
.container .sidebar .menu-list-block .logo .toggle-btn svg {
  width: 100%;
  height: 100%;
  fill: #fff;
}
.container .sidebar .menu-list-block .menu-container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: calc(100% - 150px);
}
.container .sidebar .wallet-btn {
  display: flex;
  align-items: center;
  color: #333333;
  cursor: pointer;
  margin-left: 20px;
  margin-top: 28px;
  font-weight: 600;
}
.container .sidebar .connect-wallet-btn {
  width: 152px;
  height: 38px;
  justify-content: center;
  background: #F3F7FE;
  border-radius: 10px;
}
.container .sidebar .connect-wallet-btn:hover {
  background: #dde4ff;
}
.container .sidebar .connect-wallet-btn svg {
  width: 16px;
  height: 16px;
  fill: #909DB4;
  margin-right: 8px;
}
.container .sidebar .wallet-account {
  width: 160px;
  height: 42px;
  border-radius: 5px;
  background: #f2f4fc;
}
.container .sidebar .wallet-account .network {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}
.container .sidebar .wallet-account .network i {
  display: inline-block;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background: #0083ff;
  margin-right: 4px;
}
.container .sidebar .wallet-account .address {
  width: 93px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  border: 1px solid #8d9db6;
}
.container .sidebar .total-protfolio {
  padding-left: 20px;
  margin-top: 30px;
}
.container .sidebar .total-protfolio p {
  display: flex;
  align-items: center;
  font-size: 12px;
  color: #909DB4;
  font-weight: 600;
}
.container .sidebar .total-protfolio p svg {
  width: 16px;
  height: 16px;
  fill: rgba(0, 0, 0, 0.5);
  margin-left: 7px;
}
.container .sidebar .total-protfolio .total-num {
  display: flex;
  align-items: center;
}
.container .sidebar .total-protfolio .total-num > span {
  font-size: 16px;
  color: #333333;
  font-weight: 600;
}
.container .sidebar .total-protfolio .total-num .rate {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  min-width: 43px;
  padding: 0px 4px;
  height: 16px;
  background: rgba(0, 90, 255, 0.1);
  color: #005AFF;
  font-size: 12px;
  margin-left: 4px;
  font-weight: 600;
}
.container .sidebar .total-protfolio .total-num .rate svg {
  width: 8px;
  height: 8px;
  fill: #005AFF;
  margin-left: 2px;
}
.container .sidebar .menu-list {
  padding-left: 12px;
  margin-top: 40px;
}
.container .sidebar .menu-list a {
  display: flex;
  height: 48px;
  border-radius: 24px 0px 0px 24px;
  align-items: center;
  padding-left: 8px;
  color: #909DB4;
  font-size: 14px;
}
.container .sidebar .menu-list a svg {
  width: 20px;
  height: 20px;
  fill: #909DB4;
}
.container .sidebar .menu-list a span {
  margin-left: 10px;
  font-size: 14px !important;
  font-weight: 600;
}
.container .sidebar .menu-list a img {
  width: 90px;
  height: 40px;
  z-index: 99;
}
.container .sidebar .menu-list a:hover,
.container .sidebar .menu-list a.active {
  color: #005AFF;
  background: #F3F7FE;
  font-weight: 600;
}
.container .sidebar .menu-list a:hover svg,
.container .sidebar .menu-list a.active svg {
  fill: #005AFF;
}
.container .sidebar .menu-bottom {
  padding-bottom: 20px !important;
  width: 200px;
  background: #FFFFFF;
  padding-top: 20px;
}
.container .sidebar .menu-bottom .tip {
  padding: 8px;
  border: 1px solid #ececec;
  width: 160px;
  border-radius: 8px;
  margin: auto;
  text-align: left;
}
.container .sidebar .menu-bottom .tip .tit {
  display: flex;
  justify-content: left;
  align-items: center;
}
.container .sidebar .menu-bottom .tip .tit img {
  width: 12px;
  height: 12px;
}
.container .sidebar .menu-bottom .tip .tit span {
  font-size: 12px;
  color: #909db4;
  margin-left: 3px;
}
.container .sidebar .menu-bottom .tip p {
  margin-top: 5px;
  line-height: 15px;
}
.container .sidebar .menu-bottom .docs {
  width: 160px;
  height: 38px;
  margin: 16px auto 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  background: #F3F7FE;
  color: #909DB4;
}
.container .sidebar .menu-bottom .link-list {
  display: flex;
  padding: 20px 30px 20px;
  align-items: center;
  justify-content: space-around;
}
.container .sidebar .menu-bottom .link-list a svg {
  width: 16px;
  height: 16px;
  fill: #909DB4;
}
.container .sidebar .menu-bottom .link-list a:hover svg {
  fill: #005AFF;
}
.container .sidebar .menu-bottom .theme-mode-box {
  display: flex;
  padding: 13px 20px 20px;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}
.container .sidebar .menu-bottom .theme-mode-box .mode-select {
  cursor: pointer;
}
.container .sidebar .menu-bottom .theme-mode-box .mode-select img {
  height: 25px;
}
.container .main {
  flex: 1;
  height: 100vh;
  overflow-y: auto;
}
@media (max-width: 750px) {
  .container {
    flex-direction: column;
    position: relative;
  }
  .container .menu-list-block {
    display: none;
    background: #F7F8FB;
  }
  .container .menu-list-block.show {
    display: block;
    position: fixed;
    left: 0px;
    right: 0px;
    top: 50px;
    bottom: 0px;
    z-index: 1000;
  }
  .container .sidebar {
    width: 100%;
    height: auto;
  }
  .container .sidebar .language-select {
    background: #f7f8fb;
  }
  .container .sidebar .language-select i {
    display: none;
  }
  .container .sidebar .logo {
    display: none;
  }
  .container .sidebar .wallet-btn {
    margin-top: 20px;
  }
  .container .sidebar .total-protfolio {
    display: none;
  }
  .container .sidebar .h5-nav {
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #FFFFFF;
    padding: 0px 20px;
    box-shadow: 10px 0px 50px 0px rgba(186, 200, 224, 0.2);
  }
  .container .sidebar .h5-nav > img {
    height: 28px;
  }
  .container .sidebar .h5-nav svg {
    width: 16px;
    height: 16px;
    fill: #005AFF;
  }
  .container .sidebar .menu-list {
    padding-left: 0px;
    margin-top: 20px;
  }
  .container .sidebar .menu-list a {
    border-bottom: 1px solid #e9edf5;
    border-radius: 0px;
    padding-left: 22px;
  }
  .container .sidebar .menu-list a:first-child {
    border-top: 1px solid #e9edf5;
  }
  .container .sidebar .menu-bottom {
    width: 100%;
    background: none;
  }
  .container .sidebar .menu-bottom .docs {
    background: #F2F4FC;
  }
  .container .sidebar .menu-bottom .link-list {
    width: 160px;
    margin: 0 auto;
  }
  .container .main {
    width: 100%;
  }
}
