/** @format */
.trade {
  width: 400px;
  padding: 20px 16px;
  background: #fff;
  border-radius: 8px;
  margin-left: 20px;
  height: 100%;
}
.trade .trade-title {
  display: flex;
  justify-content: space-between;
}
.trade .trade-title .title {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
}
.trade .trade-title svg {
  width: 20px;
  height: 20px;
}
.trade .amount {
  width: 100%;
  margin-top: 20px;
  padding: 20px 12px;
  background: #F2F4FC;
  border-radius: 8px;
  border: 1px solid transparent;
}
.trade .amount .amount-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #909DB4;
  font-weight: 600;
  margin-bottom: 5px;
}
.trade .amount .amount-header .balance {
  font-weight: 600;
}
.trade .amount .amount-header button {
  width: 40px;
  height: 20px;
  background: #d3e2ff;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  color: #005aff;
  font-size: 12px;
  font-weight: 600;
  padding: 0px;
  margin-left: 5px;
}
.trade .amount .amount-header button:hover {
  background-color: #005aff;
  color: #ffffff;
}
.trade .amount .amount-header .ant-btn[disabled] {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.1);
}
.trade .amount .trade-price {
  display: flex;
  justify-content: space-between;
}
.trade .amount .trade-price .ant-skeleton {
  margin-top: 3px;
}
.trade .amount .trade-price input {
  font-size: 20px;
  font-weight: 600;
  color: #333333;
  padding: 0px;
}
.trade .amount .trade-price .select-box {
  display: flex;
  align-items: center;
  justify-content: center;
}
.trade .amount .trade-price .select-box img {
  width: 24px;
  height: 24px;
}
.trade .amount .trade-price .select-box .ant-select-selection-item {
  font-size: 16px;
  font-weight: 600;
  color: #333333;
}
.trade .amount .trade-price .select-box .ant-select-selection-item img {
  margin-right: 10px;
  width: 24px;
  height: 24px;
}
.trade .amount .trade-price .select-box .ant-select-selection-item span {
  font-size: 16px;
  font-weight: 600;
  color: #333333;
}
.trade .amount .trade-price .select-box span {
  font-size: 16px;
  font-weight: 600;
  color: #333333;
}
.trade .amount .trade-price .select-box .ant-select-arrow {
  margin-top: -7px;
}
.trade .amount .trade-price .select-box .ant-select-arrow .icon {
  fill: rgba(0, 0, 0, 0.5);
}
.trade .amount-active {
  border: 1px solid #4788ff;
}
.trade .icon {
  width: 16px;
  height: 16px;
}
.trade .collateral {
  margin-top: 20px;
}
.trade .collateral .collateral-ratio-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.trade .collateral .collateral-ratio-header .collateral-title- {
  font-weight: 600;
}
.trade .collateral .collateral-ratio-header .input-box {
  display: flex;
  align-items: center;
  padding: 0 12px 0 1px;
  border-radius: 8px;
  border: 1px solid #ececec;
  color: #ffffff;
}
.trade .collateral .collateral-ratio-header .input-box .ant-input-suffix {
  color: #333333;
  font-weight: 600;
}
.trade .collateral .collateral-ratio-header .input-box .ant-input-affix-wrapper {
  width: auto;
}
.trade .collateral .collateral-ratio-header .input-box input {
  width: 40px;
  font-size: 16px;
  font-weight: 500;
  color: #333333;
  font-weight: 600;
}
.trade .collateral .collateral-ratio-header .input-box .input-btn-max {
  padding: 4px 16px;
  background: rgba(0, 255, 198, 0.1);
  border-radius: 4px;
  color: #00b590;
  font-weight: 600;
}
.trade .collateral .collateral-ratio-header .input-box .input-btn-middle {
  padding: 4px 16px;
  background: rgba(255, 214, 0, 0.1);
  border-radius: 4px;
  color: #ffd600;
  font-weight: 600;
}
.trade .collateral .collateral-ratio-header .input-box .input-btn-min {
  padding: 4px 16px;
  background: rgba(254, 12, 8, 0.1);
  border-radius: 4px;
  color: #fe0c08;
  font-weight: 600;
}
.trade .collateral .collateral-ratio-header .input-box-active {
  border: 1px solid #4788ff;
}
.trade .collateral .collateral-ratio-header .input-box-slide-active {
  border: 1px solid #fe0c08;
}
.trade .collateral .collateral-slider {
  margin-top: 16px;
}
.trade .tx-fee {
  display: flex;
  justify-content: space-between;
  padding: 21px 8px;
  background-color: #F7F7F7;
  border-radius: 8px;
  margin-top: 12px;
}
.trade .tx-fee .tx-fee-text {
  font-size: 12px;
  font-weight: 600;
  color: #909DB4;
}
.trade .tx-fee .tx-fee-price {
  font-size: 12px;
  font-weight: 500;
  color: #333333;
}
.trade .available {
  margin-top: 20px;
  width: 100%;
  background: #fee6e6;
  border-radius: 8px;
}
.trade .available .content {
  color: #ff0000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.trade .available .content span {
  line-height: 26px;
}
.trade .available .content img {
  width: 14px;
  height: 14px;
  margin-right: 10px;
}
.trade .available .content svg {
  width: 12px;
  height: 12px;
  margin-left: 2px;
  fill: #909DB4;
}
.trade .confirm-order {
  margin-top: 20px;
  width: 100%;
  height: 48px;
  font-size: 16px !important;
  font-weight: 600;
  color: #fff;
  background: #005aff;
  border-radius: 8px;
}
.trade .confirm-order:hover {
  background: #0d8fff;
}
.trade .confirm-order span {
  font-size: 16px !important;
}
.trade .ant-btn[disabled] span {
  color: #737373;
}
.trade .ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
}
.trade .ant-btn[disabled],
.trade .ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
  color: #fff;
}
@media (max-width: 750px) {
  .trade {
    width: 100%;
    margin-left: 0;
    margin-top: 20px;
  }
  .trade .amount {
    padding: 10px;
  }
  .trade .confirm-order {
    width: 100%;
  }
}
