/** @format */
.accountCard-container .ant-input {
  background-color: #F2F4FC;
  height: 40px;
  border-radius: 8px;
}
.accountCard-container .base {
  display: flex;
  justify-content: space-between;
}
.accountCard-container .base p {
  font-size: 14px;
  font-weight: 600;
  color: #000;
  line-height: 14px;
}
.accountCard-container .base .change {
  width: 60px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  background: #005AFF;
  border-radius: 4px;
  opacity: 0.4;
}
.accountCard-container .btn {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.accountCard-container .btn .ant-btn {
  width: 150px;
  height: 40px;
  border-radius: 8px;
  opacity: 0.2;
  border: 1px solid #000;
}
.accountCard-container .btn .ant-btn span {
  color: #000;
}
.accountCard-container .btn .ant-btn:nth-child(2) {
  background: #005AFF;
}
@media (max-width: 750px) {
  .logo-img {
    margin-top: 0px;
  }
}
