/** @format */
.claim-container .logo-img {
  width: 42px;
  height: 42px;
  margin: 10px 0;
}
.claim-container .text {
  font-size: 14px;
  font-weight: 600;
  color: #909DB4;
  margin-top: 10px;
  line-height: 14px;
  height: 100px;
  background: #f7f7f7;
  border-radius: 8px;
  margin-top: -30px;
  padding-top: 25px;
}
.claim-container .text .textp {
  font-size: 14px;
  font-weight: 600;
  color: #909DB4;
  margin-top: 10px;
  line-height: 14px;
  height: 25px;
}
.claim-container .text span {
  font-size: 18px;
  font-weight: 500;
  color: #333333;
  line-height: 18px;
}
.claim-container .claim-btn {
  width: 100%;
  height: 48px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 20px;
  font-weight: 600;
  color: #FFFFFF;
}
.claim-container .claim-btn span {
  font-size: 16px;
}
.claim-container .claim-btn:hover {
  background-color: #0d8fff;
}
@media (max-width: 750px) {
  .logo-img {
    margin-top: 0px;
  }
}
