/** @format */
.wallet-list .wallet-item {
  display: flex;
  align-items: center;
  background: #F2F4FC;
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 8px 16px 8px 20px;
  position: relative;
  cursor: pointer;
}
.wallet-list .wallet-item:not(:first-child) {
  margin-top: 20px;
}
.wallet-list .wallet-item:hover {
  background: #dde4ff;
}
.wallet-list .wallet-item:focus {
  border: 1px solid #005AFF;
}
.wallet-list .wallet-item:focus:after {
  content: '';
  width: 8px;
  height: 8px;
  background: #005AFF;
  position: absolute;
  right: 16px;
  border-radius: 50%;
}
.wallet-list .wallet-item img {
  width: 32px;
  height: 32px;
}
.wallet-list .wallet-item .wallet-name {
  margin-left: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #333333;
}
.wallet-account-box .platform,
.wallet-account-box .account {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.wallet-account-box .platform p {
  font-size: 14px;
  font-weight: 600;
}
.wallet-account-box .platform button {
  width: 60px;
  height: 20px;
  background: #d3e2ff;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  color: #005aff;
  font-size: 12px;
  font-weight: 600;
  padding: 0px;
  margin-left: 5px;
}
.wallet-account-box .platform button:hover {
  background-color: #005aff;
  color: #ffffff;
}
.wallet-account-box .account {
  height: 48px;
  margin-top: 20px;
  background: #F7F7F7;
  padding: 0px 12px;
  color: #000;
}
.wallet-account-box .account a {
  color: #909db4;
  cursor: pointer;
}
.wallet-account-box .account p {
  font-weight: 600;
}
.account-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.account-footer button,
.account-footer a {
  width: 180px;
  height: 40px;
  cursor: pointer;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
}
.account-footer span {
  font-size: 14px;
}
.account-footer a {
  display: block;
  background: #005AFF;
  color: #fff;
  text-align: center;
  line-height: 40px;
}
@media (max-width: 750px) {
  .wallet-account-box .account p {
    width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
