/** @format */
.ant-empty-normal {
  height: 63px;
}
.ant-empty-normal .ant-empty-image {
  width: 55px;
  height: 63px;
  margin: auto;
  background-image: url('../img/common/img-none@3x.png');
  background-size: 100% 100%;
}
.ant-empty-normal .ant-empty-image .ant-empty-img-simple {
  display: none;
}
.walletZhanWei {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.walletZhanWei img {
  width: 60px;
  height: 60px;
  margin: auto;
}
.walletZhanWei button {
  margin-bottom: 20px;
  width: 120px;
  height: 40px;
  background: #005aff;
  color: #fff;
  font-weight: 600;
  margin-top: 20px;
  border-radius: 8px;
  padding: 0;
}
.walletZhanWei button span {
  text-align: center;
  font-size: 14px;
}
.walletZhanWei button:hover {
  background: #0d8fff;
  color: #fff;
}
.walletZhanWei button:active {
  background: #0d8fff;
  color: #fff;
}
.walletZhanWei button:focus {
  background: #0d8fff;
  color: #fff;
}
.tableTitle {
  font-size: 14px;
  height: 54px;
  line-height: 54px;
  margin-left: 20px;
}
.ant-table-thead > tr > th {
  padding: 16px 20px;
}
.ant-input-group-addon {
  background: transparent;
  border: none;
}
.ant-btn[disabled] span {
  color: #737373;
}
.ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
}
.ant-btn[disabled],
.ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
  color: #fff;
}
.ant-btn[disabled] {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  font-weight: 600;
  border-radius: 8px;
  color: rgba(0, 0, 0, 0.1);
}
.ant-pagination-item a {
  line-height: unset;
}
.ant-table-pagination {
  padding-top: 20px;
  padding-right: 20px;
}
.ant-notification-notice-icon {
  top: 21px;
}
.ant-notification-notice-description {
  margin-top: 0;
}
.ant-notification-notice-message {
  height: 20px;
  padding-top: 4px;
  margin-bottom: 0;
}
.ant-notification-notice-close {
  top: 28px;
}
.ant-slider-rail {
  background: #e1e1e1;
}
.input-btn-max {
  padding: 4px 16px;
  background: rgba(0, 255, 198, 0.1);
  border-radius: 4px;
  color: #00b590;
  font-weight: 600;
}
.input-btn-middle {
  padding: 4px 16px;
  background: rgba(255, 214, 0, 0.1);
  border-radius: 4px;
  color: #ffd600;
  font-weight: 600;
}
.input-btn-min {
  padding: 4px 16px;
  background: rgba(254, 12, 8, 0.1);
  border-radius: 4px;
  color: #fe0c08;
  font-weight: 600;
}
.collateral-slider-min .ant-slider-track {
  background: #fd130d;
}
.collateral-slider-middle .ant-slider-track {
  background: #ffd600;
}
.collateral-slider-max .ant-slider-track {
  background: #005aff;
}
.collateral-slider-min .ant-slider:hover .ant-slider-track {
  background: #fd130d;
}
.collateral-slider-middle .ant-slider:hover .ant-slider-track {
  background: #ffd600;
}
.collateral-slider-max .ant-slider:hover .ant-slider-track {
  background: #005aff;
}
.ant-slider-mark-text:nth-child(1) {
  left: 40% !important;
}
.ant-slider-mark-text:nth-child(2) {
  left: 68% !important;
}
.ant-slider-mark {
  position: relative;
  z-index: 5;
}
.ant-slider-mark .tips-img {
  width: 15px;
  height: 15px;
}
.ant-slider-mark .tips-img:hover + .tips-texts {
  display: block;
}
.ant-slider-mark .tips-texts {
  padding: 10px 12px;
  width: 200px;
  background: #ecf0f7;
  border-radius: 4px;
  line-height: 1;
  text-align: left;
  color: #909DB4;
  position: absolute;
  top: 30px;
  z-index: 99;
  line-height: 1.2;
  display: none;
}
.ant-modal-content {
  border-radius: 8px;
  overflow: hidden;
  text-align: center;
}
.ant-modal-content .ant-modal-header {
  text-align: left;
  padding: 20px;
  padding-bottom: 0;
  color: #666;
}
.ant-modal-content .ant-modal-header .ant-modal-title {
  font-weight: 550;
  color: #666;
}
.ant-modal-body {
  padding: 20px;
}
.ant-modal-footer {
  padding: 0px 20px 20px;
}
.ant-notification-notice {
  border-radius: 8px;
  padding: 24px 24px;
}
.ant-notification-notice .notification-icon {
  width: 24px;
  height: 24px;
}
.ant-notification-notice .ant-notification-notice-message {
  font-size: 16px;
  font-weight: 500;
  color: #333333;
  line-height: 1;
}
.ant-notification-notice .ant-notification-notice-description {
  margin-top: 6px;
  font-size: 12px;
  font-weight: 500;
  color: #909db4;
}
.ant-notification-notice .ant-notification-notice-description .description-text {
  display: flex;
  align-items: center;
}
.ant-notification-notice .ant-notification-notice-description .description-text .icon {
  width: 16px;
  height: 16px;
  fill: #909db4;
  margin-left: 6px;
}
.customize-option-label-item {
  display: flex;
  align-items: center;
  justify-content: center;
}
.customize-option-label-item img {
  width: 16px;
  height: 16px;
  margin-right: 8px;
}
.customize-option-label-item span {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  line-height: 30px;
}
.customize-option-label-item:hover span {
  color: #005aff;
}
.ant-select-dropdown {
  background: #fbfcff !important;
  border-radius: 8px;
  border: 1px solid #005aff;
  padding: 0;
}
.ant-skeleton .ant-skeleton-content .ant-skeleton-title {
  margin: 0;
  width: auto !important;
}
.ant-skeleton .ant-skeleton-content .ant-skeleton-title + .ant-skeleton-paragraph {
  margin: 0;
}
.table-cell {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
}
.table-cell .ant-btn[disabled] {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.1);
}
.table-cell img {
  width: 40px;
  height: 40px;
  margin-right: 5px;
  border: #909db4;
  border-radius: 8px;
}
.table-cell .short {
  width: 48px;
  height: 16px;
  background: #005aff;
  border-radius: 5px;
  color: #fff;
  padding: 0;
  margin-left: 10px;
  line-height: unset;
}
.table-cell .short span {
  font-size: 12px;
}
.table-cell .short:hover {
  background: #0d8fff;
  color: #fff;
}
.table-cell .collateral-slider-min .ant-slider-track {
  background: #fd130d !important;
}
.table-cell .collateral-slider-middle .ant-slider-track {
  background: #ffd600 !important;
}
.table-cell .collateral-slider-max .ant-slider-track {
  background: #005aff !important;
}
.table-cell .ant-slider-handle {
  width: 4px;
  height: 10px;
  margin-top: -3px;
  background: #a9a9a9;
  border-radius: 0;
  border: none;
}
.table-cell .balance-usd {
  font-size: 12px;
  font-weight: 500;
  color: #909db4;
  margin-top: 14px;
}
.table-cell .Claim {
  cursor: pointer;
  color: #005aff;
  margin-top: 14px;
}
.table-cell .Claim:hover {
  color: #0d8fff;
}
.table-cell .blanceName {
  display: flex;
}
.table-cell .blanceName .warningBalance {
  display: flex;
  color: #ff5c5c;
}
.table-cell .blanceName .warningBalance .blance-text {
  position: relative;
}
.table-cell .blanceName .warningBalance .blance-text .warning {
  margin-left: 3px;
  margin-top: -2px;
  width: 12px;
  height: 12px;
  cursor: pointer;
}
.table-cell .blanceName .warningBalance .blance-text:hover .warningText {
  display: block;
}
.table-cell .blanceName .warningBalance .blance-text .warningText {
  position: absolute;
  top: -80px;
  left: -80px;
  width: 230px;
  background: #f7f7f7;
  color: #909DB4;
  border-radius: 8px;
  padding: 10px;
  z-index: 99;
  display: none;
}
.table-cell .slider-text {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.table-cell .slider-text .risk-level {
  width: 63px;
  height: 20px;
  background: rgba(0, 255, 198, 0.1);
  border-radius: 4px;
  font-weight: 500;
  color: #00b590;
  text-align: center;
  line-height: 20px;
}
.table-cell .action-btn {
  min-width: 86px;
  height: 28px;
  background: #005aff;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 600;
  color: #ffffff;
  line-height: 1;
}
