/** @format */
.calculator-container .calculator-card {
  font-size: 14px;
  font-weight: 600;
  color: #909DB4;
  line-height: 14px;
  height: 175px;
  background: #f7f7f7;
  border-radius: 8px;
  padding: 20px;
}
.calculator-container .calculator-card .item {
  display: flex;
  justify-content: space-between;
}
.calculator-container .calculator-card .item .itemp {
  font-size: 14px;
  font-weight: 400;
  color: #909DB4;
  line-height: 14px;
  height: 30px;
}
.calculator-container .calculator-card .item span {
  font-size: 14px;
  color: #333333;
  font-weight: 600;
  line-height: 14px;
}
.calculator-container .calculator-card .item:nth-child(1) .itemp:nth-child(1) {
  color: #333333;
  font-weight: 600;
}
.calculator-container .calculator-describe {
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  color: #909DB4;
  line-height: 20px;
  margin-top: 20px;
}
.calculator-container .calculator-btn {
  width: 100%;
  height: 48px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 20px;
  font-weight: 600;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
}
.calculator-container .calculator-btn span {
  font-size: 16px;
}
.calculator-container .calculator-btn:hover {
  background-color: #0d8fff;
}
.calculator-container .calculator-btn svg {
  margin-left: 5px;
  width: 16px;
  height: 16px;
  fill: #FFFFFF;
}
@media (max-width: 750px) {
  .logo-img {
    margin-top: 0px;
  }
}
