/** @format */
.chooseAssetPair-container ::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
.chooseAssetPair-container ::-webkit-scrollbar-thumb {
  background-color: #909DB4;
  border-radius: 3px;
}
.chooseAssetPair-container .ant-input {
  background-color: #F2F4FC;
  height: 40px;
  border-radius: 8px;
}
.chooseAssetPair-container .base {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.chooseAssetPair-container .base p {
  font-size: 14px;
  font-weight: 550;
  color: #333333;
  line-height: 14px;
  margin-top: 20px;
}
.chooseAssetPair-container .asset {
  display: flex;
  margin-top: 12px;
}
.chooseAssetPair-container .asset .asset-item {
  padding: 5px 10px;
  height: 42px;
  background: #f2f4fc;
  border-radius: 8px;
  border: 1px solid transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 16px;
  cursor: pointer;
}
.chooseAssetPair-container .asset .asset-item img {
  width: 28px;
  height: 28px;
}
.chooseAssetPair-container .asset .asset-item span {
  font-size: 14px;
  font-weight: 500;
  color: #333333;
  line-height: 14px;
  margin-left: 8px;
}
.chooseAssetPair-container .card {
  height: 100%;
  max-height: 292px;
  background: #F2F4FC;
  border-radius: 10px;
  margin-top: 20px;
  overflow: auto;
}
.chooseAssetPair-container .card .item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  padding: 5px 15px 0 5px;
  border: 1px solid transparent;
  border-radius: 10px;
  cursor: pointer;
}
.chooseAssetPair-container .card .item:hover {
  background-color: #e4ebfc;
  border-radius: 10px;
}
.chooseAssetPair-container .card .item .left {
  display: flex;
}
.chooseAssetPair-container .card .item .left .logo {
  width: 36px;
  height: 36px;
}
.chooseAssetPair-container .card .item .left .tit {
  margin-left: 10px;
  margin-top: 2px;
  text-align: left;
}
.chooseAssetPair-container .card .item .left .tit p {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  line-height: 18px;
  margin-bottom: 5px;
}
.chooseAssetPair-container .card .item .left .tit span {
  font-size: 12px;
  color: #666666;
  line-height: 12px;
}
.chooseAssetPair-container .card .item .right {
  text-align: right !important;
}
.chooseAssetPair-container .card .item .right p {
  font-size: 14px;
  font-weight: 500;
  color: #333333;
  line-height: 18px;
  margin-bottom: 5px;
}
.chooseAssetPair-container .card .item .right svg {
  width: 10px;
  height: 10px;
  fill: #909DB4;
  margin-right: 3px;
}
.chooseAssetPair-container .card .item .right span {
  font-size: 12px;
  font-weight: 400;
  color: #909DB4;
  line-height: 12px;
}
@media (max-width: 750px) {
  .logo-img {
    margin-top: 0px;
  }
}
