/** @format */
.title {
  font-size: 18px;
  font-weight: 600;
  color: #333333;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title .position {
  position: relative;
}
.title .position span {
  font-size: 12px;
  font-weight: 600;
  color: #909db4;
}
.title .position .manage {
  color: #333333;
  font-size: 18px;
  margin-right: 40px;
}
.title .position .farm {
  color: #333333;
  font-size: 18px;
  margin-right: 65px;
}
.title .position svg {
  fill: #909db4;
  width: 16px;
  height: 16px;
  position: absolute;
  bottom: -3px;
  left: 80px;
}
.title .icon {
  min-width: 16px;
  max-width: 16px;
  height: 16px;
  fill: #909DB4;
  margin-left: 8px;
}
.title .h5-account {
  display: none;
}
.title .pc-account {
  display: block;
}
@media (max-width: 750px) {
  .title {
    width: 100%;
    margin-top: 20px;
  }
  .title .h5-account {
    display: block;
  }
  .title .pc-account {
    display: none;
  }
  .title .address span {
    width: 80%;
  }
}
