/** @format */
.waitings-modal img,
.success-modal img,
.rejected-modal img {
  margin-top: 16px;
  width: 60px;
  height: 60px;
}
.waitings-modal p,
.success-modal p,
.rejected-modal p {
  margin-top: 16px;
  margin-bottom: 12px;
  font-size: 16px;
  font-weight: 500;
  color: #333333;
}
.waitings-modal p span,
.success-modal p span,
.rejected-modal p span {
  font-size: 14px;
  font-weight: 600;
  color: #005aff;
}
.waitings-modal p:nth-of-type(2),
.success-modal p:nth-of-type(2),
.rejected-modal p:nth-of-type(2) {
  margin-top: 12px;
}
.waitings-modal a,
.success-modal a,
.rejected-modal a {
  font-size: 14px;
  font-weight: 600;
  color: #005aff;
  line-height: 30px;
}
.waitings-modal .modal-btn,
.success-modal .modal-btn,
.rejected-modal .modal-btn {
  width: 160px;
  height: 40px;
  background: #005aff;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #ffffff;
  margin-top: 40px;
}
.waitings-modal .modal-btn span,
.success-modal .modal-btn span,
.rejected-modal .modal-btn span {
  font-size: 14px;
}
.rejected-modal p {
  margin-bottom: 0;
}
