/** @format */
.staking-left .staking-left-title {
  font-size: 16px;
  font-weight: 600;
  color: #909DB4;
  padding-bottom: 20px;
  border-bottom: 1px solid #F3F7FE;
  display: flex;
}
.staking-left .staking-left-title span {
  font-size: 16px;
}
.staking-left .staking-left-title img {
  width: 14px;
  height: 14px;
  margin-left: 8px;
}
.staking-left .staking-left-title .tip {
  position: relative;
}
.staking-left .staking-left-title .tip .tip-img {
  position: absolute;
  top: 0;
  right: -20px;
  width: 14px;
  height: 14px;
  cursor: pointer;
}
.staking-left .staking-left-title .tip:hover .tip-hover {
  display: block;
}
.staking-left .staking-left-title .tip .tip-hover {
  display: none;
  width: 467px;
  height: 130px;
  background: #ecf0f7;
  color: #909DB4;
  z-index: 9999;
  padding: 10px;
  position: absolute;
  top: 0px;
  right: -495px;
  border-radius: 8px;
}
.staking-left .staking-left-title .tip .tip-hover p {
  font-weight: 400;
  line-height: 20px;
  margin-top: 15px;
}
.staking-left .staking-left-title .tip .tip-hover p:nth-child(1) {
  margin-top: 0px;
}
.staking-left .nsdx-price {
  padding: 17px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 12px;
  background: #F7F7F7;
  border-radius: 8px;
}
.staking-left .nsdx-price .nsdx-text {
  font-size: 14px;
  font-weight: 500;
  color: #333333;
}
.staking-left .nsdx-price .nsdx-usd {
  font-size: 18px;
  font-weight: bold;
  color: #333333;
}
.staking-left .nsdx-price .nsdx-usd span {
  font-size: 14px;
  font-weight: 500;
  color: #909DB4;
  margin-right: 3px;
}
.staking-left .claimable-rewards {
  padding: 16px 12px;
  background: #F7F7F7;
  border-radius: 8px;
  margin-top: 20px;
  text-align: center;
}
.staking-left .claimable-rewards .total-nsdx {
  margin-top: 8px;
}
.staking-left .nsdx-usd {
  margin-top: 16px;
}
.staking-left .nsdx-usd .nsdx-num {
  font-weight: bold;
  color: #333333;
  font-size: 18px;
}
.staking-left .nsdx-usd .nsdx-num-usd {
  font-size: 14px;
  font-weight: 500;
  color: #909db4;
  margin-top: 8px;
}
.staking-left .auto-nsdx {
  width: 300px;
  padding: 20px 20px;
  border-radius: 10px;
  display: block;
  background: #fff;
}
.staking-left .auto-nsdx .claim-btn {
  width: 100%;
  height: 48px;
  background: #005aff;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #ffffff;
  margin-top: 16px;
}
.staking-left .auto-nsdx .claim-btn:hover {
  background: #0d8fff;
}
.staking-left .auto-nsdx .claim-btn span {
  font-size: 14px;
}
.staking-left .auto-nsdx .ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
}
.staking-left .auto-nsdx .ant-btn[disabled],
.staking-left .auto-nsdx .ant-btn[disabled]:hover,
.staking-left .auto-nsdx .ant-btn[disabled]:focus,
.staking-left .auto-nsdx .ant-btn[disabled]:active {
  background: rgba(0, 0, 0, 0.05);
  color: #737373;
}
.staking-left .nsdx-balance {
  width: 300px;
  padding: 20px 20px;
  border-radius: 10px;
  display: block;
  background: #fff;
}
@media (max-width: 750px) {
  .staking-left {
    display: none;
  }
}
