/** @format */
.manageRight-close-container {
  width: 400px;
  padding: 20px 16px;
  background: #fff;
  border-radius: 8px;
}
.manageRight-close-container .tx-fee {
  padding: 20px 20px;
  background-color: #F7F7F7;
  border-radius: 8px;
}
.manageRight-close-container .tx-fee .item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.manageRight-close-container .tx-fee .item .tx-fee-text {
  font-size: 12px;
  font-weight: 600;
  color: #909DB4;
}
.manageRight-close-container .tx-fee .item .tx-fee-price {
  font-size: 12px;
  font-weight: 500;
  color: #333333;
}
.manageRight-close-container .tx-fee .item:nth-last-child(1) {
  margin-bottom: 0;
}
.manageRight-close-container .available {
  margin-top: 20px;
  width: 100%;
  background: #fee6e6;
  border-radius: 8px;
}
.manageRight-close-container .available .content {
  color: #ff0000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.manageRight-close-container .available .content span {
  line-height: 26px;
}
.manageRight-close-container .available .content img {
  width: 14px;
  height: 14px;
  margin-right: 10px;
}
.manageRight-close-container .available .content svg {
  width: 16px;
  height: 16px;
  margin-left: 2px;
  fill: #ff0000;
}
.manageRight-close-container .close {
  margin-top: 20px;
  width: 100%;
  height: 48px;
  font-size: 16px !important;
  font-weight: 600;
  color: #fff;
  background: #005aff;
  border-radius: 8px;
}
.manageRight-close-container .close:hover {
  background: #0d8fff;
}
.manageRight-close-container .close span {
  font-size: 16px !important;
}
.manageRight-close-container .ant-btn[disabled] span {
  color: #737373;
}
.manageRight-close-container .ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
}
.manageRight-close-container .ant-btn[disabled],
.manageRight-close-container .ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
  color: #fff;
}
@media (max-width: 750px) {
  .manageRight-close-container {
    width: 100%;
    margin-left: 0;
    margin-top: 20px;
  }
  .manageRight-close-container .amount {
    padding: 10px;
  }
  .manageRight-close-container .confirm-order {
    width: 100%;
  }
}
