/** @format */
.long-container {
  width: 400px;
  padding: 20px 16px;
  background: #fff;
  border-radius: 8px;
}
.long-container .short-desc {
  font-size: 12px;
  color: #909db4;
  margin-bottom: 12px;
  line-height: 20px;
  display: inline-block;
}
.long-container .short-desc svg {
  fill: #80adff;
  width: 16px;
  height: 16px;
}
.long-container .short-desc span {
  color: #005aff;
  font-weight: 600;
  cursor: pointer;
}
.long-container .amount {
  width: 100%;
  margin-top: 20px;
  padding: 20px 12px;
  background: #F2F4FC;
  border-radius: 8px;
  border: 1px solid transparent;
}
.long-container .amount:nth-child(1) {
  margin-top: 0;
}
.long-container .amount .amount-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #909DB4;
  font-weight: 600;
  margin-bottom: 5px;
}
.long-container .amount .amount-header .balance {
  font-weight: 600;
}
.long-container .amount .amount-header button {
  width: 40px;
  height: 20px;
  background: #d3e2ff;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  color: #005aff;
  font-size: 12px;
  font-weight: 600;
  padding: 0px;
  margin-left: 5px;
}
.long-container .amount .amount-header button:hover {
  background-color: #005aff;
  color: #ffffff;
}
.long-container .amount .amount-header .ant-btn[disabled] {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.1);
}
.long-container .amount .trade-price {
  display: flex;
  justify-content: space-between;
}
.long-container .amount .trade-price .ant-skeleton {
  margin-top: 3px;
}
.long-container .amount .trade-price input {
  font-size: 20px;
  font-weight: 600;
  color: #333333;
  padding: 0px;
}
.long-container .amount .trade-price .select-box {
  display: flex;
  align-items: center;
  justify-content: center;
}
.long-container .amount .trade-price .select-box img {
  width: 24px;
  height: 24px;
}
.long-container .amount .trade-price .select-box .ant-select-selection-item {
  font-size: 16px;
  font-weight: 600;
  color: #333333;
}
.long-container .amount .trade-price .select-box .ant-select-selection-item img {
  margin-right: 10px;
  width: 24px;
  height: 24px;
}
.long-container .amount .trade-price .select-box .ant-select-selection-item span {
  font-size: 16px;
  font-weight: 600;
  color: #333333;
}
.long-container .amount .trade-price .select-box span {
  font-size: 16px;
  font-weight: 600;
  color: #333333;
}
.long-container .amount .trade-price .select-box .ant-select-arrow {
  margin-top: -7px;
}
.long-container .amount .trade-price .select-box .ant-select-arrow .icon {
  fill: rgba(0, 0, 0, 0.5);
}
.long-container .long-add {
  display: block;
  width: 16px;
  height: 16px;
  margin: 10px auto -10px;
}
.long-container .long-add svg {
  fill: #909db4;
}
.long-container .amount-active {
  border: 1px solid #4788ff;
}
.long-container .icon {
  width: 16px;
  height: 16px;
}
.long-container .collateral {
  margin-top: 20px;
}
.long-container .collateral .collateral-ratio-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.long-container .collateral .collateral-ratio-header .collateral-title- {
  font-weight: 600;
}
.long-container .collateral .collateral-ratio-header .input-box {
  display: flex;
  align-items: center;
  padding: 0 12px 0 1px;
  border-radius: 8px;
  border: 1px solid #ececec;
  color: #ffffff;
}
.long-container .collateral .collateral-ratio-header .input-box .ant-input-suffix {
  color: #333333;
  font-weight: 600;
}
.long-container .collateral .collateral-ratio-header .input-box .ant-input-affix-wrapper {
  width: auto;
}
.long-container .collateral .collateral-ratio-header .input-box input {
  width: 40px;
  font-size: 16px;
  font-weight: 500;
  color: #333333;
  font-weight: 600;
}
.long-container .collateral .collateral-ratio-header .input-box .input-btn-max {
  padding: 4px 16px;
  background: rgba(0, 255, 198, 0.1);
  border-radius: 4px;
  color: #00b590;
  font-weight: 600;
}
.long-container .collateral .collateral-ratio-header .input-box .input-btn-middle {
  padding: 4px 16px;
  background: rgba(255, 214, 0, 0.1);
  border-radius: 4px;
  color: #ffd600;
  font-weight: 600;
}
.long-container .collateral .collateral-ratio-header .input-box .input-btn-min {
  padding: 4px 16px;
  background: rgba(254, 12, 8, 0.1);
  border-radius: 4px;
  color: #fe0c08;
  font-weight: 600;
}
.long-container .collateral .collateral-ratio-header .input-box-active {
  border: 1px solid #4788ff;
}
.long-container .collateral .collateral-ratio-header .input-box-slide-active {
  border: 1px solid #fe0c08;
}
.long-container .collateral .collateral-slider {
  margin-top: 16px;
}
.long-container .tx-fee {
  padding: 20px 20px;
  background-color: #F7F7F7;
  border-radius: 8px;
  margin-top: 20px;
}
.long-container .tx-fee .item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.long-container .tx-fee .item .tx-fee-text {
  font-size: 12px;
  font-weight: 600;
  color: #909DB4;
}
.long-container .tx-fee .item .tx-fee-price {
  font-size: 12px;
  font-weight: 600;
  color: #333333;
  display: flex;
  align-items: center;
}
.long-container .tx-fee .item .tx-fee-price .icon {
  width: 18px;
  height: 18px;
  margin-left: 5px;
}
.long-container .tx-fee .item:nth-last-child(1) {
  margin-bottom: 0;
}
.long-container .long-farm {
  margin-top: 20px;
  width: 100%;
  height: 48px;
  font-size: 16px !important;
  font-weight: 600;
  color: #fff;
  background: #005aff;
  border-radius: 8px;
}
.long-container .long-farm:hover {
  background: #0d8fff;
}
.long-container .long-farm span {
  font-size: 16px !important;
}
.long-container .ant-btn[disabled] span {
  color: #737373;
}
.long-container .ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
}
.long-container .ant-btn[disabled],
.long-container .ant-btn[disabled]:hover {
  background: rgba(0, 0, 0, 0.05);
  color: #fff;
}
@media (max-width: 750px) {
  .long-container {
    width: 100%;
    margin-left: 0;
    margin-top: 20px;
  }
  .long-container .amount {
    padding: 10px;
  }
  .long-container .confirm-order {
    width: 100%;
  }
}
