/** @format */
.claimBounty-container .claimBountyTop {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.claimBounty-container .claimBountyTop .left {
  font-size: 18px;
  color: #333333;
  font-weight: 550;
}
.claimBounty-container .claimBountyTop .right {
  text-align: right;
}
.claimBounty-container .claimBountyTop .right p {
  font-size: 18px;
  color: #333333;
  font-weight: 600;
  height: 30px;
}
.claimBounty-container .claimBountyTop .right .rightSpan {
  font-size: 14px;
  color: #909DB4;
}
.claimBounty-container .text {
  color: #909DB4;
  margin-top: 20px;
  line-height: 14px;
  height: 80px;
  background: #f7f7f7;
  border-radius: 8px;
  padding: 20px 10px 20px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.claimBounty-container .text .item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}
.claimBounty-container .text .item span {
  color: #909DB4;
  font-size: 14px;
  font-weight: 500;
}
.claimBounty-container .text .item p {
  color: #333333;
  font-size: 14px;
  font-weight: 550;
}
.claimBounty-container .claimBounty-btn {
  width: 100%;
  height: 48px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 20px;
  font-weight: 600;
  color: #FFFFFF;
}
.claimBounty-container .claimBounty-btn span {
  font-size: 16px;
}
.claimBounty-container .claimBounty-btn:hover {
  background-color: #0d8fff;
}
@media (max-width: 750px) {
  .logo-img {
    margin-top: 0px;
  }
}
