/** @format */
.staking-right {
  width: 580px;
  margin-left: 20px;
}
.staking-right .stake {
  height: 140px;
  background: url('../../img/stake/pc-liquidity-banner.png') no-repeat;
  background-size: 100% auto;
  padding-top: 83px;
}
.staking-right .stake .stake-apr {
  display: flex;
  justify-content: space-around;
  text-align: center;
}
.staking-right .stake .stake-apr .apr-title {
  width: 100%;
  font-size: 16px;
  font-weight: bold;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}
.staking-right .stake .stake-apr .apr-title .icon {
  width: 16px;
  height: 16px;
  fill: #FFFFFF;
  margin-left: 4px;
}
.staking-right .stake .stake-apr .text {
  font-size: 12px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 9px;
}
.staking-right .stake-action {
  padding: 20px;
  display: flex;
  background-color: #FFFFFF;
}
.staking-right .stake-action .stake-action-list {
  background: #f7f7f7;
  border-radius: 8px;
}
.staking-right .stake-action .stake-action-list .stake-action-item {
  width: 132px;
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  height: 46px;
  text-align: center;
  line-height: 46px;
  cursor: pointer;
}
.staking-right .stake-action .stake-action-list .stake-action-item-active {
  background: #E6EFFF;
  color: #333333;
  border-radius: 8px;
  border: none;
  height: 46px;
}
.staking-right .stake-action .stake-action-num {
  margin-left: 20px;
  width: 100%;
}
.staking-right .stake-action .stake-action-num .amount {
  background: #F2F4FC;
  border-radius: 8px;
  padding: 20px 12px;
  height: 80px;
  margin-top: 0;
}
.staking-right .stake-action .stake-action-num .amount .amount-header {
  font-weight: 600;
  color: #909db4;
  display: flex;
  justify-content: space-between;
}
.staking-right .stake-action .stake-action-num .amount .amount-header span {
  font-weight: 600;
  color: #333333;
}
.staking-right .stake-action .stake-action-num .amount .amount-input {
  display: flex;
  justify-content: space-between;
  margin-top: 0px;
}
.staking-right .stake-action .stake-action-num .amount .amount-input input {
  font-size: 18px;
  font-weight: 600;
  color: #333333;
  padding: 0;
}
.staking-right .stake-action .stake-action-num .amount .amount-input .max-btn {
  padding: 4px 8px;
  max-height: 20px;
  background: #d6e4ff;
  border-radius: 4px;
  font-weight: 600;
  color: #005AFF;
  margin-top: 6px;
  cursor: pointer;
}
.staking-right .stake-action .stake-action-num button {
  margin-top: 12px;
  width: 100%;
  height: 48px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #FFFFFF;
}
.staking-right .stake-action .stake-action-num button span {
  font-size: 14px;
}
.staking-right .amount-header {
  font-weight: 600;
  color: #909db4;
  display: flex;
  justify-content: space-between;
}
.staking-right .amount-header span {
  font-weight: 600;
  color: #333333;
}
.staking-right .amount-input {
  display: flex;
  justify-content: space-between;
  margin-top: 8px;
}
.staking-right .amount-input input {
  font-size: 18px;
  font-weight: 600;
  color: #333333;
  padding: 0;
}
.staking-right .amount-input .max-btn {
  padding: 4px 8px;
  max-height: 20px;
  background: rgba(0, 90, 255, 0.23);
  border-radius: 4px;
  font-weight: 600;
  color: #005AFF;
  cursor: pointer;
}
.staking-right button {
  margin-top: 12px;
  width: 100%;
  height: 48px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #FFFFFF;
}
.liquidity .liquidity-banner {
  background: url('../../img/stake/pc-liquidity-banner.png') no-repeat;
  padding-top: 84px;
  background-size: 100% 100%;
  height: 140px;
  text-align: center;
  border-radius: 8px;
}
.liquidity .liquidity-banner .total-liquidity-title {
  font-size: 18px;
  font-weight: 700;
  color: #FFFFFF;
}
.liquidity .liquidity-banner .total-liquidity-text {
  font-size: 12px;
  font-weight: 500;
  color: #FFFFFF;
  margin-top: 8px;
}
.liquidity .liquidity-banner-zh {
  background: url('../../img/stake/stakeBg.png') no-repeat;
  background-size: 100% 100%;
}
.liquidity .liquidity-content {
  padding: 20px;
  background: #FFFFFF;
  border-radius: 10px;
}
.liquidity .liquidity-content .liquidity-item {
  padding: 16px 12px;
  background: #F2F4FC;
  border-radius: 8px;
  margin-bottom: 20px;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo img {
  width: 54px;
  height: 32px;
  margin-right: 8px;
  margin-top: 0px;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo .nas-auto-logo {
  width: 42px;
  height: 42px;
  position: absolute;
  top: 0;
  left: 0;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo .liquidity-naxlogo-img {
  width: 32px;
  height: 32px;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo .auto {
  position: absolute;
  top: -3px;
  right: 0;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo .auto img {
  position: absolute;
  top: 0;
  right: 0;
  width: 92px;
  height: 40px;
  cursor: pointer;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo .auto:hover .auto-hover {
  display: block;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo .auto .auto-hover {
  display: none;
  width: 186px;
  height: 100px;
  background: #ecf0f7;
  color: #909DB4;
  border-radius: 8px;
  z-index: 99;
  padding: 10px;
  line-height: 20px;
  font-weight: 600;
  position: absolute;
  top: 40px;
  right: 0;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-name {
  font-size: 18px;
  font-weight: bold;
  color: #333333;
  margin-top: 0;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-name-auto {
  position: absolute;
  top: 8px;
  left: 48px;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-source {
  width: auto;
  height: 28px;
  background: #d6e4ff;
  padding: 0 12px;
  font-weight: 600;
  color: #909db4;
  border: 1px solid transparent;
  border-radius: 15px;
  cursor: pointer;
  border-image-slice: 1;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-source:hover {
  border: 1px solid #005aff;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-source a {
  width: auto;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #909db4;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-source a img {
  width: 18px;
  height: 18px;
  margin: 0 3px;
}
.liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-source a .icon {
  width: 16px;
  height: 16px;
  fill: #909DB4;
  margin-left: 6px;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom {
  display: flex;
  margin-top: 20px;
  align-items: center;
  justify-content: space-between;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom .apr {
  margin-left: 50px;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom .title {
  width: 120px;
  white-space: nowrap;
  text-align: center;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom .title:hover svg {
  fill: #005AFF;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom .text {
  margin-top: 8px;
  font-weight: 500;
  color: #909db4;
  text-align: center;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom button {
  width: 122px;
  height: 48px;
  margin-left: 90px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-weight: 600;
  color: #FFFFFF;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom button span {
  font-size: 14px;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom button:hover {
  background-color: #0d8fff;
}
.liquidity .liquidity-content .liquidity-item .liquidity-bottom-auto {
  margin-top: 62px;
}
.liquidity .liquidity-content .liquidity-item .h5-stake-btn {
  display: none;
}
.liquidity .liquidity-content .liquidity-item .line {
  display: block;
  height: 1px;
  width: 100%;
  background: #dbe1ea;
  margin: 20px 0;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim {
  width: 48%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 12px;
  background: #FFFFFF;
  border-radius: 8px;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left span {
  color: #909DB4;
  font-size: 12px;
  line-height: 12px;
  font-weight: 550;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left p {
  width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
  color: #333333;
  font-size: 14px;
  line-height: 14px;
  font-weight: 550;
  margin-top: 5px;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left p:nth-of-type(2) {
  font-size: 12px;
  color: #909db4;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left .tip {
  position: relative;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left .tip .tip-img {
  position: absolute;
  top: -13px;
  right: -20px;
  width: 14px;
  height: 14px;
  cursor: pointer;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left .tip:hover .tip-hover {
  display: block;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left .tip .tip-hover {
  display: none;
  width: 206px;
  height: 80px;
  background: #ecf0f7;
  z-index: 9999;
  padding: 10px;
  position: absolute;
  top: 6px;
  right: -215px;
  border-radius: 8px;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left .tip .tip-hover p {
  width: 100%;
  font-weight: 400;
  line-height: 20px;
  font-size: 12px;
  color: #909DB4;
  margin-top: 0;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .ant-btn {
  width: 79px;
  height: 32px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-weight: 600;
  color: #FFFFFF;
  padding: 0;
  margin-top: 0;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .ant-btn span {
  font-size: 14px;
}
.liquidity .liquidity-content .liquidity-item .claim-unstake .claim .ant-btn:hover {
  background-color: #0d8fff;
}
.liquidity .liquidity-content .liquidity-item-lp {
  margin-bottom: 0;
}
@media (max-width: 750px) {
  .staking-container-center {
    width: 100%;
  }
  .staking-right {
    width: 100%;
    margin: 0;
  }
  .staking-right .stake {
    height: 86px;
    padding: 45px 0 0 0;
    background: url('../../img/stake/h5-banner@2x.png') no-repeat;
    background-size: 100% auto;
  }
  .staking-right .stake .stake-apr .apr-title {
    font-size: 14px;
  }
  .staking-right .stake .stake-apr .text {
    font-size: 12px;
    margin-top: 8px;
  }
  .staking-right .ant-tabs-left > .ant-tabs-content-holder > .ant-tabs-content > .ant-tabs-tabpane,
  .staking-right .ant-tabs-left > div > .ant-tabs-content-holder > .ant-tabs-content > .ant-tabs-tabpane {
    padding-left: 12px;
  }
  .staking-right .stake-action .stake-action-list .ant-tabs-nav {
    border-radius: 8px;
    background: #f7f7f7;
  }
  .staking-right .stake-action .stake-action-list .ant-tabs-nav-list {
    width: 66px;
  }
  .staking-right .stake-action .stake-action-list .ant-tabs-nav-list .ant-tabs-tab {
    width: 66px;
    height: 33px;
  }
  .staking-right .stake-action .stake-action-list .ant-tabs-nav-list .ant-tabs-tab-active {
    height: 33px;
  }
  .staking-right .stake-action .stake-action-list .ant-tabs-nav-list .ant-tabs-tab-btn {
    font-size: 12px;
    line-height: 33px;
  }
  .staking-right .stake-action .stake-action-num .amount {
    padding: 10px 10px;
  }
  .staking-right .stake-action .stake-action-num .amount .amount-header span {
    font-size: 12px;
  }
  .staking-right .stake-action .stake-action-num button {
    height: 40px;
  }
  .staking-right .liquidity .liquidity-banner {
    background: url('../../img/stake/h5-liquidity-banner.png') no-repeat;
    background-size: 100% auto;
    padding-top: 45px;
    height: 86px;
  }
  .staking-right .liquidity .liquidity-banner .total-liquidity-title {
    font-size: 14px;
  }
  .staking-right .liquidity .liquidity-banner .total-liquidity-text {
    font-size: 12px;
  }
  .staking-right .liquidity .liquidity-banner-zh {
    background: url('../../img/stake/stakeBg.png') no-repeat;
    background-size: 100% auto;
  }
  .staking-right .liquidity .liquidity-content {
    padding: 10px;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .liquidity-header {
    width: 100%;
    display: block;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo,
  .staking-right .liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-source {
    width: 230px;
    margin: auto;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .liquidity-header .liquidity-logo {
    margin-bottom: 10px;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .liquidity-bottom {
    justify-content: space-around;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .liquidity-bottom .pc-stake-btn {
    display: none;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .h5-stake-btn {
    margin-top: 12px;
    display: block;
    width: 100%;
    height: 40px;
    background: #005aff;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    color: #ffffff;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .claim-unstake {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .claim-unstake .claim {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 12px;
    background: #FFFFFF;
    border-radius: 8px;
    margin-top: 20px;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .claim-unstake .claim:nth-child(1) {
    margin-top: 0px;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .claim-unstake .claim .left .tip-hover {
    left: 100px;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .claim-unstake .claim .ant-btn {
    height: 32px;
    margin-left: 90px;
    background: #005AFF;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    font-weight: 600;
    text-align: center;
    padding: 0;
    color: #FFFFFF;
  }
  .staking-right .liquidity .liquidity-content .liquidity-item .claim-unstake .claim .ant-btn:hover {
    background-color: #0d8fff;
  }
}
