/** @format */
.farm-setting {
  text-align: left;
}
.farm-setting .farm-setting-content .slippage-tolerance {
  font-size: 14px;
  font-weight: 400;
  color: #333333;
}
.farm-setting .farm-setting-content .input-content {
  margin-top: 12px;
  display: flex;
  justify-content: space-between;
}
.farm-setting .farm-setting-content .input-content .slippage-item {
  width: 72px;
  height: 32px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  color: #333333;
  text-align: center;
  line-height: 32px;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.05);
}
.farm-setting .farm-setting-content .input-content .slippage-item-active {
  border: 1px solid #005aff;
  color: #005aff;
}
.farm-setting .farm-setting-content .input-content input {
  width: 72px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
}
.farm-setting .farm-setting-content .input-content .ant-input-affix-wrapper {
  width: 72px;
  border-radius: 8px;
}
.farm-setting .tips-available {
  margin-top: 20px;
  width: 100%;
  padding: 5px;
  background: rgba(254, 96, 18, 0.1);
  border-radius: 8px;
}
.farm-setting .tips-available .content {
  color: #fe5d0d;
  display: flex;
  justify-content: center;
  align-items: center;
}
.farm-setting .tips-available .content span {
  line-height: 18px;
}
.farm-setting .tips-available .content img {
  width: 14px;
  height: 14px;
  margin-right: 10px;
}
.farm-setting .tips-available .content svg {
  width: 12px;
  height: 12px;
  margin-left: 2px;
  fill: #909DB4;
}
.farm-setting .available {
  margin-top: 20px;
  width: 100%;
  background: #fee6e6;
  border-radius: 8px;
}
.farm-setting .available .content {
  color: #ff0000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.farm-setting .available .content span {
  line-height: 26px;
}
.farm-setting .available .content img {
  width: 14px;
  height: 14px;
  margin-right: 10px;
}
.farm-setting .available .content svg {
  width: 12px;
  height: 12px;
  margin-left: 2px;
  fill: #909DB4;
}
.farm-setting .deadline {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.farm-setting .deadline p {
  font-size: 14px;
  font-weight: 400;
  color: #333333;
}
.farm-setting .deadline .ant-input {
  width: 72px !important;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
}
.farm-setting .farm-setting-alert {
  margin-top: 12px;
  color: #ff4343;
}
.farm-setting .btnBox {
  margin-top: 20px;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.farm-setting .btnBox button {
  cursor: pointer;
  width: 45%;
  height: 35px;
  font-size: 14px !important;
  font-weight: 500;
  color: #fff;
  background: #005aff;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.05);
}
.farm-setting .btnBox button:hover {
  background: #0d8fff;
}
.farm-setting .btnBox button:nth-child(1) {
  color: #000;
  background: #fff;
  border: 1px solid #005aff;
}
.farm-setting .btnBox button:nth-child(1):hover {
  background: #f2f4fc;
  color: #005aff;
}
