/** @format */
.wallet-list .wallet-item {
  display: flex;
  align-items: center;
  background: #F2F4FC;
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 8px 16px 8px 20px;
  position: relative;
  cursor: pointer;
}
.wallet-list .wallet-item:not(:first-child) {
  margin-top: 20px;
}
.wallet-list .wallet-item:hover {
  background: #dde4ff;
}
.wallet-list .wallet-item:focus {
  border: 1px solid #005AFF;
}
.wallet-list .wallet-item:focus:after {
  content: '';
  width: 8px;
  height: 8px;
  background: #005AFF;
  position: absolute;
  right: 16px;
  border-radius: 50%;
}
.wallet-list .wallet-item img {
  width: 32px;
  height: 32px;
}
.wallet-list .wallet-item .wallet-name {
  margin-left: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #333333;
}
