/** @format */
.manageRight-table-content {
  width: 400px;
  padding: 10px 0px;
  background: #fff;
  border-radius: 8px;
  margin-left: 20px;
  height: 100%;
}
.manageRight-table-content .icon {
  width: 16px;
  height: 16px;
  fill: #909db4;
  margin-right: 5px;
}
.manageRight-table-content .action-btn + .action-btn {
  margin-left: 20px;
}
.manageRight-table-content .table-header-tab {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 35px 0 20px;
  margin-bottom: 0;
}
.manageRight-table-content .table-header-tab svg {
  width: 20px;
  height: 20px;
}
.manageRight-table-content .table-header-tab .header-tab-item {
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: #909db4;
  display: flex;
  align-items: center;
  position: relative;
  padding: 10px 0px;
}
.manageRight-table-content .table-header-tab .header-tab-item-active {
  font-size: 14px;
  font-weight: 500;
  color: #005aff;
}
.manageRight-table-content .table-header-tab .header-tab-item-active .icon {
  fill: #005aff;
}
.manageRight-table-content .table-header-tab .header-tab-item-active::after {
  content: '';
  width: 20px;
  height: 2px;
  background: #005aff;
  border-radius: 1px;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}
@media (max-width: 750px) {
  .manageRight-table-content {
    width: 100%;
    margin-left: 0;
    margin-top: 20px;
  }
  .manageRight-table-content .amount {
    padding: 10px;
  }
  .manageRight-table-content .confirm-order {
    width: 100%;
  }
}
