/** @format */
.network-container .text {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  margin: 20px 0;
  line-height: 1.5;
}
.network-container .btn {
  width: 160px;
  height: 40px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 20px;
  font-weight: 600;
  color: #FFFFFF;
}
@media (max-width: 750px) {
  .waring-img {
    margin-top: 0px;
  }
}
