/** @format */
.unstake-container {
  position: relative;
}
.unstake-container svg {
  position: absolute;
  top: -43px;
  right: 35px;
  width: 20px;
  height: 20px;
}
.unstake-container .img {
  width: 42px;
  height: 42px;
  margin: 10px 0;
}
.unstake-container .liquidity-logo {
  display: flex;
  align-items: center;
}
.unstake-container .liquidity-logo img {
  width: 44px;
  height: 44px;
  margin-right: 8px;
}
.unstake-container .liquidity-name {
  font-size: 14px;
  font-weight: 550;
  color: #333333;
}
.unstake-container .amount {
  background: #F2F4FC;
  border-radius: 8px;
  padding: 20px 12px;
  margin-top: 12px;
}
.unstake-container .amount .amount-header {
  font-weight: 600;
  color: #909db4;
  display: flex;
  justify-content: space-between;
}
.unstake-container .amount .amount-header span {
  font-weight: 600;
  color: #333333;
  font-size: 16px;
}
.unstake-container .amount .amount-input {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 8px;
}
.unstake-container .amount .amount-input input {
  font-size: 18px;
  font-weight: 600;
  color: #333333;
  padding: 0;
  margin-bottom: -5px;
}
.unstake-container .amount .amount-input .max-btn {
  padding: 4px 8px;
  max-height: 20px;
  background: #d6e4ff;
  border-radius: 4px;
  font-weight: 600;
  color: #005AFF;
  line-height: 1;
  font-size: 12px;
}
.unstake-container .amount .amount-input .max-btn:hover {
  background-color: #005aff;
  color: #ffffff;
}
.unstake-container .amount .amount-input .ant-btn[disabled] {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.1);
}
.unstake-container .Recevied {
  display: flex;
  margin-top: 15px;
  justify-content: space-between;
}
.unstake-container .Recevied .amount-tit {
  font-weight: 600;
  font-size: 14px;
}
.unstake-container .Recevied .asset {
  display: flex;
  flex-direction: column;
}
.unstake-container .Recevied .asset span {
  font-weight: 600;
  color: #333333;
  line-height: 25px;
  font-size: 14px;
  text-align: right;
}
.unstake-container .stake-btn {
  width: 100%;
  height: 48px;
  background: #005AFF;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 20px;
  font-weight: 600;
  color: #FFFFFF;
}
.unstake-container .stake-btn span {
  font-size: 16px;
}
.unstake-container .stake-btn:hover {
  background-color: #0d8fff;
}
.unstake-container .ant-btn[disabled] {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  font-weight: 600;
  box-shadow: none;
  border-radius: 8px;
  color: rgba(0, 0, 0, 0.1);
}
@media (max-width: 750px) {
  .img {
    margin-top: 0px;
  }
}
