/** @format */
.tikerInfo-container .top {
  display: flex;
  justify-content: space-between;
}
.tikerInfo-container .top .left {
  display: flex;
}
.tikerInfo-container .top .left .logo {
  width: 36px;
  height: 36px;
}
.tikerInfo-container .top .left .tit {
  margin-left: 10px;
  margin-top: 2px;
  text-align: left;
}
.tikerInfo-container .top .left .tit .titp {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  line-height: 14px;
  height: 18px;
}
.tikerInfo-container .top .left .tit span {
  font-size: 12px;
  color: #666666;
  line-height: 12px;
}
.tikerInfo-container .top h5 {
  font-size: 18px;
  font-weight: 600;
  color: #333333;
  line-height: 36px;
  margin-bottom: 0;
}
.tikerInfo-container .card {
  font-size: 14px;
  font-weight: 600;
  color: #909DB4;
  margin-top: 10px;
  line-height: 14px;
  height: 55px;
  background: #f7f7f7;
  border-radius: 8px;
  padding: 20px;
}
.tikerInfo-container .card .item {
  display: flex;
  justify-content: space-between;
}
.tikerInfo-container .card .item .itemp {
  font-size: 14px;
  font-weight: 400;
  color: #666;
  line-height: 14px;
  height: 30px;
}
.tikerInfo-container .card .item span {
  font-size: 14px;
  color: #333333;
  font-weight: 600;
  line-height: 14px;
}
.tikerInfo-container .describe {
  text-align: left;
  font-size: 14px;
  font-weight: 400;
  color: #666666;
  line-height: 20px;
  margin-top: 20px;
}
@media (max-width: 750px) {
  .logo-img {
    margin-top: 0px;
  }
}
