/** @format */
.farm-pool-list {
  display: flex;
  flex-wrap: wrap;
}
.farm-pool-list .farm-pool-item {
  width: 33%;
  max-width: 320px;
  margin-top: 20px;
  padding: 20px;
  background: #FFFFFF;
  border-radius: 10px;
  margin-right: 20px;
}
.farm-pool-list .farm-pool-item:nth-child(3n) {
  margin-right: 0px;
}
.farm-pool-list .farm-pool-item .pool-header {
  display: flex;
}
.farm-pool-list .farm-pool-item .pool-header .pool-logo {
  width: 32px;
  height: 32px;
  margin-right: 10px;
}
.farm-pool-list .farm-pool-item .pool-header .pool-name {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
}
.farm-pool-list .farm-pool-item .pool-header .pool-source {
  font-size: 12px;
  font-weight: 400;
  color: #909DB4;
  margin-top: 6px;
}
.farm-pool-list .farm-pool-item .farmPoolBox {
  background: #F7F7F7;
  border-radius: 8px;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  padding: 0 12px;
}
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px 0px;
}
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .short-apr,
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .short-TVL {
  margin-top: 15px;
}
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .long-apr-title,
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .long-TVL-title {
  font-size: 16px;
  font-weight: 600;
  color: #333333;
  line-height: 16px;
}
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .long-apr-text {
  font-size: 12px;
  font-weight: 600;
  color: #909DB4;
  margin-top: 6px;
}
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .long-TVL-title {
  width: 120px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .long-TVL,
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .short-TVL {
  position: relative;
}
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info :hover .hover {
  display: block;
}
.farm-pool-list .farm-pool-item .farmPoolBox .apr-info .hover {
  background: #fff;
  padding: 8px;
  border-radius: 4px;
  color: #000;
  font-size: 14px;
  position: absolute;
  top: -30px;
  left: 0;
  display: none;
}
.farm-pool-list .farm-pool-item .amm-price {
  display: flex;
  justify-content: space-between;
}
.farm-pool-list .farm-pool-item .amm-price:not(:last-child) {
  margin-top: 20px;
}
.farm-pool-list .farm-pool-item .amm-price .leabl {
  font-size: 14px;
  font-weight: 500;
  color: #909DB4;
}
.farm-pool-list .farm-pool-item .amm-price .text {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
}
.farm-pool-list .farm-pool-item .amm-price .premium {
  position: relative;
}
.farm-pool-list .farm-pool-item .amm-price p {
  display: none;
  width: 190px;
  position: absolute;
  background-color: #f7f7f7;
  top: 20px;
  left: 0;
  z-index: 99;
  padding: 10px;
  border-radius: 8px;
}
.farm-pool-list .farm-pool-item .amm-price img {
  width: 16px;
  height: 16px;
  margin-left: 5px;
}
.farm-pool-list .farm-pool-item .amm-price:hover p {
  display: block;
}
.farm-pool-list .farm-pool-item .action-btn {
  margin-top: 20px;
  width: 100%;
}
.farm-pool-list .farm-pool-item .action-btn .farmBtn {
  width: 46%;
  height: 48px;
  background: #005AFF;
  border-radius: 8px;
  font-weight: 900;
  color: #FFFFFF;
  border: none;
}
.farm-pool-list .farm-pool-item .action-btn .farmBtn span {
  font-size: 14px;
  font-weight: 900;
}
.farm-pool-list .farm-pool-item .action-btn .farmBtn:hover {
  background: #0d8fff;
}
.farm-pool-list .farm-pool-item .action-btn .shortBtn {
  margin-left: 16px;
}
.farm-pool-list .farm-pool-item .action-btn .longBtn {
  width: 100%;
}
@media (max-width: 750px) {
  .farm-pool-list {
    justify-content: center;
  }
  .farm-pool-list .farm-pool-item {
    width: 355px;
    margin-right: 0;
  }
}
